<template>
  <div>
    <PageTitle/>

    <div class="page-box">
      <PanelTitle title="예측 위험도 높은 설비" :has-underline="true"/>
      <div class="container">
        <div class="table-container">
          <PanelSubTitle title="24시간"/>

          <table class="default-table">
            <thead>
            <tr>
              <th class="w80">No.</th>
              <th>설비</th>
              <th class="w100">위험률</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item, index) in predictEquipmentRiskTop5.hour24" :key="index">
              <td>{{index + 1}}</td>
              <td>{{item.procs_nm}}<span class="arrow">&gt;</span>{{item.eqmt_nm}}</td>
              <td>
                <RiskStepLabel :code="item.risk_step_cd" :name="item.risk_num + '%'" />
              </td>
            </tr>
            </tbody>
          </table>
        </div>
        <div class="table-container">
          <PanelSubTitle title="7일"/>

          <table class="default-table">
            <thead>
            <tr>
              <th class="w80">No.</th>
              <th>설비</th>
              <th class="w100">위험률</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item, index) in predictEquipmentRiskTop5.day7" :key="index">
              <td>{{index + 1}}</td>
              <td>{{item.procs_nm}}<span class="arrow">&gt;</span>{{item.eqmt_nm}}</td>
              <td>
                <RiskStepLabel :code="item.risk_step_cd" :name="item.risk_num + '%'" />
              </td>
            </tr>
            </tbody>
          </table>
        </div>
        <div class="table-container">
          <PanelSubTitle title="4주"/>

          <table class="default-table">
            <thead>
            <tr>
              <th class="w80">No.</th>
              <th>설비</th>
              <th class="w100">위험률</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item, index) in predictEquipmentRiskTop5.week4" :key="index">
              <td>{{index + 1}}</td>
              <td>{{item.procs_nm}}<span class="arrow">&gt;</span>{{item.eqmt_nm}}</td>
              <td>
                <RiskStepLabel :code="item.risk_step_cd" :name="item.risk_num + '%'" />
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <div class="page-box">
      <PanelTitle title="위험도 예측"/>
      <PredictionFiltering @filtering="onFiltering"/>

      <PanelSubTitle title="24시간 예측"/>
      <ChartBox :raw-data="predictEquipmentHour24.list" chart-type="PREDICTION24HOUR"/>
      <table class="default-table double-row-header">
        <thead>
        <tr>
          <th rowspan="2">날짜</th>
          <th v-for="item in ymdList"
              :key="item.ymd"
              :colspan="item.count">
            {{ formatter.date(item.ymd, 'MM/dd') }}
          </th>
        </tr>
        <tr>
          <th v-for="(item, idx) in predictEquipmentHour24.list"
              :key="idx">
            {{ item.qtime }}시
          </th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td>위험률</td>
          <td v-for="item in predictEquipmentHour24.list" :key="item.qtime">
            <RiskLabel :text-data="item.risk_rate" is-double-row/>
          </td>
        </tr>
        </tbody>
      </table>

      <PanelSubTitle title="7일 예측"/>
      <ChartBox :raw-data="predictEquipmentDay7.list" chart-type="PREDICTION7DAY"/>
      <table class="default-table double-row-header">
        <thead>
        <tr>
          <th rowspan="2">날짜</th>
          <th v-for="(item, index) in predictEquipmentDay7.list"
              :key="index">
            {{ formatter.date(item.ymd, 'MM/dd') }}
            <span v-if="index === 0">(오늘)</span>
          </th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td>위험률</td>
          <td v-for="(item, index) in predictEquipmentDay7.list" :key="index">
            <RiskLabel :text-data="item.risk_rate" is-double-row/>
          </td>
        </tr>
        </tbody>
      </table>

      <PanelSubTitle title="4주 예측"/>
      <ChartBox :raw-data="predictEquipmentWeek4.list" chart-type="PREDICTION4WEEK"/>
      <table class="default-table double-row-header">
        <thead>
        <tr>
          <th rowspan="2">날짜</th>
          <th v-for="(item, index) in predictEquipmentWeek4.list"
              :key="index">
            {{ formatter.date(item.risk_dt, 'MM/dd~MM/dd') }}
          </th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td>위험률</td>
          <td v-for="(item, index) in predictEquipmentWeek4.list" :key="index">
            <RiskLabel :text-data="item.risk_rate" is-double-row/>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import {mapState} from "vuex";
import PanelTitle from "@/components/ui/PanelTitle";
import PanelSubTitle from "@/components/ui/PanelSubTitle";
import RiskStepLabel from "@/components/ui/RiskStepLabel";
import PredictionFiltering from "@/components/ui/PredictionFiltering";
import ChartBox from "@/components/chart/ChartBox";
import formatter from "@/modules/formatter";
import RiskLabel from "@/components/ui/RiskLabel";
import PageTitle from "@/components/panels/PageTitle";
export default {
  name: "RiskPrediction",
  components: {PageTitle, RiskLabel, ChartBox, PredictionFiltering, RiskStepLabel, PanelSubTitle, PanelTitle},

  computed: {
    ...mapState([
      'predictEquipmentRiskTop5',
      'predictEquipmentHour24',
      'predictEquipmentDay7',
      'predictEquipmentWeek4'
    ]),
    activeEquipmentId() {
      // return (this.realtimeRisk.eqmt[0]) ? this.realtimeRisk.eqmt[0].eqmt_id : '';
      return this.activeEquipId;
    },

    ymdList() {
      let list = [];
      this.predictEquipmentHour24.list.forEach(item => {
        let temp = list[list.length - 1];
        if (temp && temp.ymd === item.ymd){
          temp.count++;
        } else {
          list.push({
            ymd: item.ymd,
            count: 1
          });
        }
      })
      return list
    }
  },

  created() {
    this.$store.dispatch('predictEquipmentRiskTop5');
    this.$store.dispatch('predictEquipmentHour24');
    this.$store.dispatch('predictEquipmentDay7');
    this.$store.dispatch('predictEquipmentWeek4');
  },

  data(){
    return {
      formatter
    }
  },

  methods: {
    onFiltering(options){
      console.log(options)

      this.$store.dispatch('predictEquipmentHour24', options);
      this.$store.dispatch('predictEquipmentDay7', options);
      this.$store.dispatch('predictEquipmentWeek4', options);
    }
  }
};
</script>

<style scoped lang="scss">
@import "src/assets/scss/table";
.page-box{
  > .container{
    display: flex;
    justify-content: space-between;

    .table-container{
      width: 31%;

      span.arrow{
        padding: 0 6px;
      }
    }
  }
}

</style>